// *      ()

.logo-text {
  display: flex;
}

//           

.logo-text__image {
  path:last-child {
    fill: $text-darker;
  }
}

// *      ()

.logo-icon {
  display: flex;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

//*  Cat Energy   ()

.footer__org-logo {
  padding: 39px 0 31px;

  text-align: center;
  display: block;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

//*  HTML Academy   ()

.footer__dev-logo {
  padding: 22px 0;

  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

// * HTML Academy ()

.developer-logo {
  &:hover .developer-logo__icon-path {
    fill: $primary-color;
  }

  &:active {
    opacity: 0.3;
  }
}

//  

.developer-logo__text {
  font-size: $primary-fs;
  line-height: 18px;
  font-family: $third-font-family;
  color: $text-light-dark-1;
  text-decoration: none;
}

//  

.developer-logo__icon {
  display: flex;
}
